import React, { useState } from 'react';
import { Alert } from 'reactstrap';
import { connect } from 'react-redux';
import { actions } from '../../redux/action';
import './alert.css'

const NotificationsAlert = (props) => {

        // const [visible, setVisible] = useState(true);

        const onDismiss = () => props.setVisible();

        return (
                <Alert id="notificationAlert" color={props.alertColor} isOpen={props.visible} toggle={onDismiss}>
                        {props.alertContent}
                </Alert>
        );
}

const mapStateToProps = (state) => {
        return {
                visible: state.alertReducer.visible,
                alertContent: state.alertReducer.alertContent,
                alertColor: state.alertReducer.alertColor
        }
}
const mapDispatchToProps = (dispatch) => ({
        setVisible: (x) => dispatch(actions.setAlertVisible(x))

})

export default connect(mapStateToProps, mapDispatchToProps)(NotificationsAlert);